.mainContainer {
  width: 800px;
  margin: 200px auto;
  color: #fff;
  text-align: center; }
  .mainContainer .searchTitle {
    font-size: 40px; }
  .mainContainer .searchContainer {
    display: flex;
    text-align: left; }
    .mainContainer .searchContainer .addressContainer {
      width: 45%; }
      .mainContainer .searchContainer .addressContainer .address {
        position: relative; }
      .mainContainer .searchContainer .addressContainer .input {
        padding-left: 130px;
        border-radius: 5px 0 0 5px;
        width: calc(100% - 130px); }
    .mainContainer .searchContainer .typeContainer {
      width: 35%; }
      .mainContainer .searchContainer .typeContainer .input {
        border-radius: 0 5px 5px 0;
        border-left: 2px solid #ddd; }
    .mainContainer .searchContainer .input {
      width: 100%;
      height: 60px;
      border: none; }
    .mainContainer .searchContainer .buttonContainer {
      width: 20%;
      display: flex;
      align-items: flex-end;
      margin-left: 10px; }
      .mainContainer .searchContainer .buttonContainer .button {
        width: 100%;
        background: #F52F41;
        border: none;
        border-radius: 5px;
        color: #fff;
        height: 60px;
        font-size: 18px;
        cursor: pointer; }
